<template>
  <div class="touristpage">
  
<div class="page-wrapper default-theme sidebar-bg bg1 toggled">
        <a id="show-sidebar" @click="closeMenu" class="btn btn-sm btn-dark" href="#">
            <i class="fas fa-bars"></i>
        </a>
        <nav id="sidebar" class="sidebar-wrapper">
            <div class="sidebar-content">
                <!-- sidebar-brand  -->
                <div class="sidebar-item sidebar-brand">
                    <router-link to="/" href="#">Cey.lk</router-link>
                    <div id="close-sidebar" @click="closeMenu">
                        <i class="fas fa-times"></i>
                    </div>
                </div>
                <!-- sidebar-header  -->
                <div class="sidebar-item sidebar-header">
                    <div class="user-pic">
                        <img class="img-responsive img-rounded" :src="profileUrl" alt="User picture">
                    </div>
                    <div class="user-info">
                        <span class="user-name">
                            <strong>{{name}}</strong>
                        </span>
                        <span class="user-role"> {{email}} </span>
                        <span class="user-status">
                            <i class="fa fa-circle"></i>
                          <span>Online</span>
                        </span>
                    </div>
                </div>
            
                <!-- sidebar-menu  -->
                <div class=" sidebar-item sidebar-menu">
                    <ul>
                        <li class="header-menu">
                            <span>Menu</span>
                        </li>
                      

                        <li>
                            <router-link to="/touristpage/touristbooking">
                              <i class="fa fa-book"></i>
                                <span class="">Bookings</span>
                            </router-link>
                        </li>
                        <li>
                            <router-link to="/touristpage/touristprofile">
                                <i class="fa fa-user"></i>
                                <span class="">Tourist Profile</span>
                            </router-link>
                        </li>
                        
                        <li>
                            <router-link to="/touristpage/touristsettings">
                               <i class="fas fa-cog"></i>
                                <span class="">Settings</span>
                            </router-link>
                        </li>
                        <li>
                            <a href="#" @click="logout">
                                <i class="fa fa-power-off"></i>
                                <span>Logout</span>
                            </a>
                        </li>
                    </ul>
                </div>
                <!-- sidebar-menu  -->
            </div>
       
        </nav>
