<script>
import firebase from 'firebase';
import { db } from '../main';


export default {
  name: "TouristPage",
  data(){
    return{
      name:null,
      email: null,
      profileUrl: null
    }
  },
  created(){
     
   this.check();

  },
  methods: {
  logout(){
    firebase.auth().signOut()
    .then(
      () => {
         alert("Logout");
         
       this.$router.replace('/');
     
      }
    ).catch(
      (err) => {
        console.log(err);
      }
    )
  },
  check(){
    
   var user = firebase.auth().currentUser;
    if (user != null){
       db.collection('tourist_bios').doc(user.uid).get().then(doc => {
        this.profileUrl=doc.data().profilepic
       
       });
      this.email = user.email;
    db.collection('tourist_profiles').doc(user.uid).get().then(doc => {
      
      this.name=doc.data().user_name
    
    })
    }
    else{
       window.onload = function() {
        return "Dude, are you sure you want to leave? Think of the kittens!";
    }
       this.$router.replace('/login');

    }
   
  }
 
}
}
</script>

